/*make transparent link selection, adjust last value opacity 0 to 1.0:*/
* { -webkit-tap-highlight-color: rgba(0,0,0,0); } 

:root {
  --lh_em:1.2em;   /*default line height for components' elements everywhere*/
  --lh_rem:1.2rem; /*default line height for components everywhere          */
  --ctrlGrpSpinFactor:1.2;
  min-height:100%;
  font-size:12px;line-height:var(--lh_rem);
}

body {
  font-size:1rem;line-height:var(--lh_rem);
  font-family:'HelveticaNeue-Light', 'HelveticaNeue', Helvetica, Arial, sans-serif;
  -webkit-touch-callout:none;-webkit-text-size-adjust:none;-webkit-user-select:none;
  width:100%;
  margin:0;padding:0;
  background-color:#E4E4E4;
  display:-ms-flexbox;
  display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;
}



